.NavBar-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  width: 100%;
}

.NavBar-item {
  margin: 10px;
}

.NavBar-item a {
  text-decoration: none;
  color: black;
  -webkit-transition: 0.5s ease;
  -o-transition: 0.5s ease;
  transition: 0.5s ease;
}

.NavBar-item a:hover {
  color: black;
}

.NavBar-item a:active {
  color: black;
}

.NavBar-item a {
  text-decoration: none;
  color: gray;
}



